import { Component, OnInit } from '@angular/core';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SessionService } from 'src/app/core/session/session.service';

@Component({
  selector: 'app-unit-users-report',
  templateUrl: './unit-users-report.component.html',
  styleUrls: ['./unit-users-report.component.scss']
})

export class UnitUsersReportComponent implements OnInit {
  
  reportDataList:any;

  isDataLoaded:boolean = false;

  constructor(
    private lookupService: LookupService,
    private sessionService:SessionService
  ) { }

  formatName(name){
    return name.replace(/^\s+|\s+$/g,"");
  }

  ngOnInit() {

    let details = {
      LookupTypeId: 87, //lookuptypeid for report
      MenuName: 'UnitUser'
    }

    this.lookupService.getLookupValuesByApartmentIdLookupTypeIdMenuName(details).subscribe((res:any) => {

      // this.reportDataList = res;
      
      let _arr =  { lookupValueID: 2030, lookupValueName: 'List of Living', description: 'Gives the list of Living', statusId: 2030, name:'isLiving' };
      let _arr2 = { lookupValueID: 2031, lookupValueName: 'List of PrimaryContact', description: 'Gives the list of PrimaryContact', statusId: 2031, name:'IsPrimaryContact' };
      res.push(_arr);
      res.push(_arr2);
      this.reportDataList = res;


      this.isDataLoaded = true;

    })


  }

  getReportRedirectPath(path){
    return path.replace('/','#');
  }

}
